.cat-list {
  height: 195px;
  scrollbar-width: none;
  flex-direction: column;
  gap: 18px;
  margin-bottom: 40px;
  font-size: 14px;
  font-style: normal;
  font-weight: 500;
  line-height: 1.28;
  display: flex;
  overflow-y: scroll;
}

.cat-opt {
  color: rgba(5, 5, 5, .3);
  background-color: rgba(0, 0, 0, 0);
  border: none;
  padding: 0;
  transition: color .2s linear;
}

.cat-opt:hover, .cat-opt:focus {
  color: #9bb537;
}

.cat-btn {
  color: rgba(5, 5, 5, .5);
  background-color: rgba(0, 0, 0, 0);
  border: 1px solid rgba(5, 5, 5, .2);
  border-radius: 15px;
  margin-bottom: 24px;
  padding: 14px 29px;
  font-size: 18px;
  font-weight: 500;
  line-height: 1.21;
  transition: background-color, color .2s linear;
}

.cat-btn:hover, .cat-btn:focus {
  color: #f8f8f8;
  background-color: #9bb537;
}

.cat-list::-webkit-scrollbar {
  width: 8px;
}

.cat-list::-webkit-scrollbar-track {
  background-color: #f1f1f1;
}

.cat-list::-webkit-scrollbar-thumb {
  background: rgba(5, 5, 5, .18);
  border-radius: 20px;
}

.cat-list::-webkit-scrollbar-thumb:hover {
  background-color: #555;
}

@media screen and (min-width: 768px) {
  .cat-btn {
    margin-bottom: 30px;
    padding: 14px 28px;
  }

  .cat-list {
    width: 176px;
    height: 416px;
    gap: 24px;
    margin-bottom: 64px;
    font-size: 16px;
    font-style: normal;
    font-weight: 500;
    line-height: 1.25;
  }
}

@media screen and (min-width: 1280px) {
  .cat-btn {
    margin-bottom: 42px;
  }

  .cat-list {
    width: 235px;
    height: 416px;
  }
}

.pop-item {
  height: 67px;
  gap: 16px;
  display: flex;
}

.pop-img {
  width: 64px;
  height: 64px;
}

.pop-header {
  color: #050505;
  text-transform: uppercase;
  margin-bottom: 20px;
  font-family: Inter;
  font-size: 18px;
  font-style: normal;
  font-weight: 600;
  line-height: 1.33;
}

.pop-list {
  flex-direction: column;
  gap: 27px;
  margin-bottom: 43px;
  display: flex;
}

.pop-info > p {
  -webkit-line-clamp: 2;
  max-width: 252px;
  height: 30px;
  color: rgba(5, 5, 5, .8);
  text-overflow: ellipsis;
  -webkit-box-orient: vertical;
  font-size: 12px;
  font-style: normal;
  font-weight: 400;
  line-height: 1.33;
  display: -webkit-box;
  overflow: hidden;
}

.pop-info > h3 {
  color: #050505;
  text-transform: uppercase;
  text-overflow: ellipsis;
  word-break: break-word;
  -webkit-line-clamp: 1;
  -webkit-box-orient: vertical;
  margin-bottom: 8px;
  font-family: Inter;
  font-size: 14px;
  font-style: normal;
  font-weight: 600;
  line-height: 1.28;
  display: -webkit-box;
  overflow: hidden;
}

@media screen and (min-width: 768px) {
  .pop-item {
    gap: 8px;
  }

  .pop-info > p {
    width: 120px;
    height: 41px;
    -webkit-line-clamp: 3;
    font-size: 10px;
    line-height: 1.4;
  }

  .pop-info > h3 {
    width: 120px;
    text-overflow: ellipsis;
    word-break: break-word;
    -webkit-line-clamp: 1;
    -webkit-box-orient: vertical;
    display: -webkit-box;
    overflow: hidden;
  }

  .pop-header {
    margin-bottom: 32px;
  }

  .pop-img {
    width: 48px;
    height: 48px;
  }
}

@media screen and (min-width: 1280px) {
  .pop-info > p {
    width: 155px;
    line-height: 1.4;
  }

  .pop-img {
    width: 64px;
    height: 64px;
  }

  .pop-item {
    gap: 16px;
  }
}











